<template>
    <yd-layout class="detailGround">
        <div class="detail">
            <div class="detailContent" :class="{active:advanceTime>=0&&state==0}">
                <div class="meetingTitle">
                    <span class="leftBorder"></span>
                    <div class="title">{{formList.meetTitle}}</div>
                    <div class="noConfir" v-show="state==0">未确认</div>
                    <div class="hasConfir" v-show="state==1&&meetState!=8">已确认</div>
                    <div class="noConfir" v-show="state==3&&meetState!=8">已拒绝</div>
                    <div class="noConfir" v-show="meetState==8">已取消</div>
                </div>
                <yd-flexbox>
                    <div class="leader fisrt" v-if="formList.companyLeader != null&&formList.companyLeader != ''&&formList.companyLeader != undefined">公司领导</div>
                    <div class="material fisrt" v-if="fileLength > 0">会议材料</div>
                    <div class="video fisrt" v-if="formList.isVideoMeeting == '1'">视频会议</div>
                </yd-flexbox>
                <yd-flexbox class="startDate">
                    <div>开始时间：</div>
                    <yd-flexbox-item v-text="formList.meetBeginTime"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div>结束时间：</div>
                    <yd-flexbox-item v-text="formList.meetEndTime"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <div>会议地点：</div>
                    <yd-flexbox-item v-text="formList.meetAddress"></yd-flexbox-item>
                </yd-flexbox>
                <div class="openPart" v-show="leaderLength != 0">
                    <div class="attendLeader">参会领导：</div>
                    <div class="leaderName" :class="{active:leaderFlag==true}">
                        <p class="textPart" v-text="formList.companyLeader" :class="{oneLine:leaderLength<15}"></p>
                        <p class="openBtn" v-show="leaderLength>14" v-on:click="openClose('one')">{{openOrclose1}}</p>
                    </div>
                </div>
                <div class="openPart" v-show="attendPeople.length!=0">
                    <div class="attendLeader">参会人员：</div>
                    <div class="leaderName" :class="{active:empFlag==true}">
                        <p class="textPart" v-text="attendees" :class="{oneLine:attendees.length<15}"></p>
                        <p class="openBtn" v-show="attendees.length>14" v-on:click="openClose('two')">{{openOrclose2}}</p>
                    </div>
                </div>
                <div class="leftPosition" v-show="fileArray.length!=0">
                    <div class="meetMaterial">会议材料：</div>
                    <div class="materialName">
                        <div class="theMaterial" v-for="(item, index) in fileArray" :key="index">
                            <a v-bind:href="pafFun(item)">{{item.attachement}}</a>
                        </div>
                    </div>
                </div>
                <div class="leftPosition" v-show="state!=0" :class="{spacing:textFlag==false}">
                    <div class="meetMaterial">会议备注：</div>
                    <div class="materialName">
                        <div class="theMaterial meetingRemark">{{meetingRemark}}</div>
                    </div>
                </div>
                <div v-show="textFlag==true">
                    <trsTextarea placeholder="请填写您的会议备注" v-model="remark"></trsTextarea>
                    <div class="finsh" v-on:click="meetUpdate()">完成</div>
                </div>
                <div style="clear:both;"></div>
                <div class="addRemark" v-on:click="addRemark();" v-show="textFlag==false&&state!=0&&meetState!=8">添加会议备注</div>
            </div>
            <div class="remove" v-on:click="toManage()" v-show="state!=0"><img src="../../../static/images/close@2x.png" class="close"></div>
            <!-- <div class="bottomBtn" v-show="advanceTime>=0">
                <yd-button bgcolor="#59a5fd" @click.native="confirmAttend()">确认参会</yd-button>
                <yd-button bgcolor="#ff4677" @click.native="show=true">不参会</yd-button>
                <yd-button bgcolor="#FFC121" @click.native="toOtherAttend()">派他人参会</yd-button>
            </div> -->
        </div>
        <yd-tabbar slot="tabbar" class="tabbarBtn" v-show="advanceTime>=0&&state==0&&meetState!=8" bgcolor="#f8f8f8">
            <div class="threeBtn">
                <div class="bottomBtn bottomBtn1" @click="confirmAttend()">确认参会</div>
                <div class="bottomBtn bottomBtn2" @click="show=true">不参会</div>
                <div class="bottomBtn bottomBtn3" @click="toOtherAttend()">派他人参会</div>
            </div>
        </yd-tabbar>
        <yd-popup v-model="show" position="center" width="5.79rem">
            <div class="other">是否确认不参会</div>
            <div class="otherBtn">
                <ul>
                    <li class="yes" @click="show=false;donNotAttend()">是</li>
                    <li class="no" @click="show=false">否</li>
                </ul>
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'detailsMeeting',
    data() {
        return {
            openOrclose1: '展开',
            openOrclose2: '展开',
            leaderFlag: false,
            empFlag: false,
            remark: '',
            remarks: [],
            textFlag: false,
            formList: {},
            attendPeople: [],
            fileLength: 0,
            attendees: '', // 参会人员
            fileArray: [],
            meetingRemark: '您暂时没有添加备注',
            show: false,
            leader: '',
            leaderLength: -1,
            userIdList: [],
            state: -1,
            meetState: -1,
            advanceTime: this.$route.params.advanceTime
        }
    },
    mounted() {
        let vm = this;
        vm.formData();
        document.setTitle('会议详情');
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        ...mapMutations([
            'setAttendeeUserId'
        ]),
        // 表单数据
        formData() {
            let vm = this;
            vm.attendPeople = [];
            vm.userIdList = [];
            vm.fileArray = [];
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/mobileMeetDetail', {
                'mid': vm.$route.params.meetingID,
                'name': vm.userInfo.ssotoken
            }, function (res) {
                vm.formList = res.jsonData;
                vm.state = res.jsonData.state;
                vm.meetState = res.jsonData.meetState;
                vm.fileLength = vm.formList.MeetingFile.length;
                if (vm.formList.companyLeader == null || vm.formList.companyLeader == '' || vm.formList.companyLeader == undefined) {
                    vm.leaderLength = 0;
                } else {
                    vm.leaderLength = vm.formList.companyLeader.length;
                }
                if (vm.formList.meetingRemark != '' && vm.formList.meetingRemark != null && vm.formList.meetingRemark != undefined) {
                    vm.meetingRemark = vm.formList.meetingRemark;
                    vm.remark = vm.formList.meetingRemark;
                }
                for (let i = 0; i < vm.formList.people.length; i++) {
                    if (vm.formList.people[i].username.trim() != '') {
                        vm.attendPeople.push(vm.formList.people[i].username);
                    }
                    vm.userIdList.push(vm.formList.people[i].userid);
                }
                vm.setAttendeeUserId({
                    userIdList: vm.userIdList
                });
                vm.attendees = vm.attendPeople.join('、');
                console.log(vm.attendPeople);
                for (let i = 0; i < vm.formList.MeetingFile.length; i++) {
                    vm.fileArray.push(vm.formList.MeetingFile[i]);
                }
            });
        },
        // 会议材料
        pafFun(item) {
            return this.isIOS ? `/meetissue/${item.remark.split('.').slice(0, -1).join('.')}.pdf` : `/pdfjs/web/viewer.html?file=/meetissue/${item.remark.split('.').slice(0, -1).join('.')}.pdf`;
        },
        // 修改备注
        meetUpdate() {
            let vm = this;
            if (vm.remark == '') {
                vm.$dialog.toast({
                    mes: '请填写您的会议备注',
                    timeout: 2000
                });
            } else {
                vm.textFlag = false;
                vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/mobileMeetUpdate', {
                    'meetsid': vm.$route.params.meetingID,
                    'remark': vm.remark,
                    'stat': vm.formList.state,
                    'name': vm.userInfo.ssotoken
                }, function (res) {
                    vm.$dialog.toast({
                        mes: '添加成功',
                        timeout: 2000,
                        callback: () => {
                            vm.formData();
                        }
                    });
                });
            }
        },
        // 确认参会
        confirmAttend() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/mobileMeetUpdate', {
                'meetsid': vm.$route.params.meetingID,
                'remark': vm.remark,
                'stat': 1,
                'name': vm.userInfo.ssotoken
            }, function (res) {
                vm.$dialog.toast({
                    mes: '已确认参会',
                    timeout: 2000,
                    callback: () => {
                        vm.$router.push('/meetingManage/');
                    }
                });
            });
        },
        // 确认不参会
        donNotAttend() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/mobileMeetUpdate', {
                'meetsid': vm.$route.params.meetingID,
                'remark': vm.remark,
                'stat': 3,
                'name': vm.userInfo.ssotoken
            }, function (res) {
                vm.$dialog.toast({
                    mes: '已确认不参会',
                    timeout: 2000,
                    callback: () => {
                        vm.$router.push('/meetingManage/');
                    }
                });
            });
        },
        openClose(num) {
            let vm = this;
            if (num == 'one') {
                vm.leaderFlag == false ? vm.leaderFlag = true : vm.leaderFlag = false;
                vm.leaderFlag == false ? vm.openOrclose1 = '展开' : vm.openOrclose1 = '收起';
            } else {
                vm.empFlag == false ? vm.empFlag = true : vm.empFlag = false;
                vm.empFlag == false ? vm.openOrclose2 = '展开' : vm.openOrclose2 = '收起';
            }
        },
        toOtherAttend() {
            let vm = this;
            vm.$router.push('/otherAttend/' + vm.$route.params.meetingID);
        },
        toManage() {
            this.$router.push('/meetingManage/');
        },
        addRemark() {
            let vm = this;
            vm.textFlag = true
            if (vm.meetingRemark == '您暂时没有添加备注') {
                vm.meetingRemark = '';
            }
        }
    }
}
</script>
<style lang="less" scoped>
.yd-btn {
    width: 25%;
    height: .85rem;
    line-height: .85rem;
    margin-right: .1rem;
    margin-bottom: .35rem;
    color: #fff;
    font-size: .3rem;
    a {
        display: block;
    }
}

.yd-scrollview {
    background-color: #f8f8f8;
}

.trsTextarea {
    min-height: 2.4rem;
    background-color: #f2f2f2;
    border-radius: 5px;
    color: #999;
    font-size: .32rem;
    padding: .4rem;
    padding-top: .15rem;
    padding-left: .2rem;
}

.finsh {
    color: #59a5fd;
    text-align: center;
    font-size: .32rem;
    margin-top: .2rem;
    line-height: .85rem;
}

.detail .detailContent .meetingTitle .title {
    width: 73%;
}

.remove {
    height: .75rem;
    width: .75rem;
    background-color: #ccc;
    margin: 0 auto;
    border-radius: .75rem;
    margin-top: .24rem;
    padding-top: .21rem;
}

.close {
    width: .32rem;
    height: .32rem;
    margin: 0 auto;
    display: block;
}

.openPart {
    min-height: 0.8rem;
    line-height: .8rem;
    overflow: hidden;
    font-size: .32rem;
    color: #333;
    .attendLeader {
        // width: 1.6rem;
        float: left;
    }

    .leaderName {
        float: left;
        width: 4.25rem;
        height: 0.8rem;
        overflow: hidden;
        .textPart {
            display: inline-block;
            float: left;
            width: 3.5rem;
            height: .8rem;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
        .oneLine {
            width: 4.25rem;
        }
        .openBtn {
            display: inline-block;
            color: #59a5fd;
            float: left;
        }
    }
    .active {
        height: auto;
        .textPart {
            height: auto;
            float: none;
            display: inline;
            white-space: pre-wrap;
        }
        .openBtn {
            // display: inline;
            float: none;
            margin-left: 0;
        }
    }
}

.fisrt {
    margin-left: .3rem;
    font-size: .28rem!important;
}

.fisrt:first-child {
    margin-left: 0;
}

.other {
    border-bottom: none!important;
}

.detailContent {
    min-height: 91%;
    position: relative;
    &.active{
        min-height: 102%;
    }
}

.noConfir,
.hasConfir {
    font-size: .24rem;
    color: #fff;
    background-color: #59a5fd;
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 4px;
    width: 1.2rem;
    height: .43rem;
    line-height: .45rem;
    text-align: center;
}

.noConfir {
    background-color: #ff3358!important;
}

.yd-tabbar {
    background-color: #f8f8f8!important;
    height: 1.29rem!important;
    padding: 0;
    .threeBtn {
        width: 100%;
        padding: 0 .4rem!important;
    }
    .bottomBtn {
        width: 2.12rem;
        height: .85rem;
        line-height: .85rem;
        margin-right: .1rem;
        margin-bottom: .3rem;
        color: #fff;
        font-size: .3rem;
        text-align: center;
        border-radius: 5px;
        float: left;
    }
    .bottomBtn1 {
        background-color: #59a5fd;
    }
    .bottomBtn2 {
        background-color: #ff4677;
    }
    .bottomBtn3 {
        background-color: #FFC121;
    }
}

.startDate {
    margin-top: .2rem;
}

.addRemark {
    position: absolute;
    bottom: 0.1rem;
    left: 2.4rem;
}

.spacing {
    padding-bottom: 1rem;
}
</style>


